import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { OnboardingFormData } from '../types';
import { useStudyContext } from '../contexts/StudyContext';

// Opes para os campos de seleo mltipla
const OBJECTIVES_OPTIONS = [
  { id: 'ansiedade', label: 'Ansiedade' },
  { id: 'familia', label: 'Famlia' },
  { id: 'sabedoria', label: 'Sabedoria' },
  { id: 'fe', label: 'Fortalecimento da F' },
  { id: 'proposito', label: 'Propsito' },
  { id: 'perdao', label: 'Perdo' },
  { id: 'paz', label: 'Paz Interior' },
  { id: 'relacionamentos', label: 'Relacionamentos' },
];

const EXPERIENCE_OPTIONS = [
  { id: 'iniciante', label: 'Iniciante (Primeiros passos na Bblia)' },
  { id: 'intermedirio', label: 'Intermedirio (Conheo o bsico)' },
  { id: 'avanado', label: 'Avanado (Estudo h muito tempo)' },
];

const CONTENT_OPTIONS = [
  { id: 'textos-curtos', label: 'Textos curtos' },
  { id: 'audio', label: 'udios explicativos' },
  { id: 'reflexao', label: 'Reflexes guiadas' },
  { id: 'perguntas', label: 'Perguntas para pensar' },
];

const TIME_OPTIONS = [
  { id: 'manh', label: 'Manh' },
  { id: 'tarde', label: 'Tarde' },
  { id: 'noite', label: 'Noite' },
  { id: 'flexvel', label: 'Flexvel' },
];

/**
 * Componente de formulrio para onboarding e criao de plano personalizado
 */
const OnboardingForm: React.FC = () => {
  const navigate = useNavigate();
  const { isLoading, createPersonalizedPlan } = useStudyContext();
  
  // Estado do formulrio multi-etapas
  const [step, setStep] = useState(1);
  const [formData, setFormData] = useState<OnboardingFormData>({
    objectives: [],
    bible_experience_level: '',
    content_preferences: [],
    preferred_time: '',
  });
  
  // Estado de validao
  const [errors, setErrors] = useState<Record<string, string>>({});
  
  // Manipuladores de eventos
  const handleObjectivesChange = (objective: string) => {
    setFormData(prev => {
      const newObjectives = prev.objectives.includes(objective)
        ? prev.objectives.filter(o => o !== objective)
        : [...prev.objectives, objective];
      
      return { ...prev, objectives: newObjectives };
    });
  };
  
  const handleExperienceChange = (level: string) => {
    setFormData(prev => ({ ...prev, bible_experience_level: level }));
  };
  
  const handleContentPreferenceChange = (preference: string) => {
    setFormData(prev => {
      const newPreferences = prev.content_preferences.includes(preference)
        ? prev.content_preferences.filter(p => p !== preference)
        : [...prev.content_preferences, preference];
      
      return { ...prev, content_preferences: newPreferences };
    });
  };
  
  const handleTimePreferenceChange = (time: string) => {
    setFormData(prev => ({ ...prev, preferred_time: time }));
  };
  
  // Validao por etapa
  const validateStep = (currentStep: number): boolean => {
    const newErrors: Record<string, string> = {};
    
    if (currentStep === 1) {
      if (formData.objectives.length === 0) {
        newErrors.objectives = 'Selecione pelo menos um objetivo';
      }
    } else if (currentStep === 2) {
      if (!formData.bible_experience_level) {
        newErrors.bible_experience_level = 'Selecione seu nvel de experincia';
      }
    } else if (currentStep === 3) {
      if (formData.content_preferences.length === 0) {
        newErrors.content_preferences = 'Selecione pelo menos uma preferncia de contedo';
      }
      if (!formData.preferred_time) {
        newErrors.preferred_time = 'Selecione um horrio preferencial';
      }
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };
  
  // Avanar para prxima etapa
  const handleNextStep = () => {
    const isValid = validateStep(step);
    
    if (isValid) {
      setStep(prev => prev + 1);
    }
  };
  
  // Voltar para etapa anterior
  const handlePrevStep = () => {
    setStep(prev => prev - 1);
  };
  
  // Enviar dados ao completar o formulrio
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    const isValid = validateStep(3);
    if (!isValid) return;
    
    try {
      // Enviar dados para criar plano personalizado
      const response = await createPersonalizedPlan(formData);
      
      if (response?.plan_id) {
        // Redirecionar para o plano criado
        navigate(`/estudo/planos/${response.plan_id}`);
      } else {
        // Redirecionar para lista de planos
        navigate('/estudo/planos');
      }
    } catch (error) {
      console.error('Erro ao criar plano personalizado', error);
    }
  };
  
  // Renderizao condicional baseada na etapa atual
  const renderStepContent = () => {
    switch (step) {
      case 1:
        return (
          <div className="space-y-6">
            <div>
              <h2 className="text-xl font-semibold text-gray-800 mb-4">
                Quais so seus objetivos?
              </h2>
              <p className="text-gray-600 mb-6">
                Selecione os temas que voc gostaria de abordar no seu estudo bblico.
              </p>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                {OBJECTIVES_OPTIONS.map(option => (
                  <button
                    key={option.id}
                    type="button"
                    onClick={() => handleObjectivesChange(option.id)}
                    className={`flex items-center px-4 py-3 rounded-lg text-left text-sm transition-colors ${
                      formData.objectives.includes(option.id)
                        ? 'bg-blue-100 border-blue-500 border text-blue-700'
                        : 'bg-white border border-gray-300 text-gray-700 hover:bg-gray-50'
                    }`}
                  >
                    {formData.objectives.includes(option.id) && (
                      <svg className="w-4 h-4 mr-2 text-blue-600" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                      </svg>
                    )}
                    {option.label}
                  </button>
                ))}
              </div>
              
              {errors.objectives && (
                <p className="mt-2 text-sm text-red-600">{errors.objectives}</p>
              )}
            </div>
          </div>
        );
        
      case 2:
        return (
          <div className="space-y-6">
            <div>
              <h2 className="text-xl font-semibold text-gray-800 mb-4">
                Qual seu nvel de conhecimento bblico?
              </h2>
              <p className="text-gray-600 mb-6">
                Isso nos ajudar a adaptar o contedo ao seu nvel de familiaridade com a Bblia.
              </p>
              
              <div className="space-y-3">
                {EXPERIENCE_OPTIONS.map(option => (
                  <button
                    key={option.id}
                    type="button"
                    onClick={() => handleExperienceChange(option.id)}
                    className={`flex items-center w-full px-4 py-3 rounded-lg text-left text-sm transition-colors ${
                      formData.bible_experience_level === option.id
                        ? 'bg-blue-100 border-blue-500 border text-blue-700'
                        : 'bg-white border border-gray-300 text-gray-700 hover:bg-gray-50'
                    }`}
                  >
                    {formData.bible_experience_level === option.id && (
                      <svg className="w-4 h-4 mr-2 text-blue-600" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                      </svg>
                    )}
                    {option.label}
                  </button>
                ))}
              </div>
              
              {errors.bible_experience_level && (
                <p className="mt-2 text-sm text-red-600">{errors.bible_experience_level}</p>
              )}
            </div>
          </div>
        );
        
      case 3:
        return (
          <div className="space-y-6">
            <div>
              <h2 className="text-xl font-semibold text-gray-800 mb-4">
                Como prefere estudar?
              </h2>
              <p className="text-gray-600 mb-3">
                Selecione os formatos que mais combinam com seu estilo de aprendizado.
              </p>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-3 mb-6">
                {CONTENT_OPTIONS.map(option => (
                  <button
                    key={option.id}
                    type="button"
                    onClick={() => handleContentPreferenceChange(option.id)}
                    className={`flex items-center px-4 py-3 rounded-lg text-left text-sm transition-colors ${
                      formData.content_preferences.includes(option.id)
                        ? 'bg-blue-100 border-blue-500 border text-blue-700'
                        : 'bg-white border border-gray-300 text-gray-700 hover:bg-gray-50'
                    }`}
                  >
                    {formData.content_preferences.includes(option.id) && (
                      <svg className="w-4 h-4 mr-2 text-blue-600" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                      </svg>
                    )}
                    {option.label}
                  </button>
                ))}
              </div>
              
              {errors.content_preferences && (
                <p className="mt-2 text-sm text-red-600">{errors.content_preferences}</p>
              )}
            </div>
            
            <div>
              <h3 className="font-medium text-gray-800 mb-3">
                Qual o melhor horrio para seu estudo dirio?
              </h3>
              
              <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                {TIME_OPTIONS.map(option => (
                  <button
                    key={option.id}
                    type="button"
                    onClick={() => handleTimePreferenceChange(option.id)}
                    className={`px-4 py-3 rounded-lg text-center text-sm transition-colors ${
                      formData.preferred_time === option.id
                        ? 'bg-blue-100 border-blue-500 border text-blue-700'
                        : 'bg-white border border-gray-300 text-gray-700 hover:bg-gray-50'
                    }`}
                  >
                    {option.label}
                  </button>
                ))}
              </div>
              
              {errors.preferred_time && (
                <p className="mt-2 text-sm text-red-600">{errors.preferred_time}</p>
              )}
            </div>
          </div>
        );
        
      default:
        return null;
    }
  };
  
  return (
    <div className="bg-white rounded-lg shadow-sm p-6">
      {/* Barra de progresso */}
      <div className="mb-8">
        <div className="flex justify-between mb-2">
          {[1, 2, 3].map(stepNumber => (
            <span
              key={stepNumber}
              className={`text-xs font-medium ${
                stepNumber <= step ? 'text-blue-600' : 'text-gray-400'
              }`}
            >
              Etapa {stepNumber}
            </span>
          ))}
        </div>
        <div className="h-2 bg-gray-200 rounded-full">
          <div
            className="h-2 bg-blue-600 rounded-full transition-all duration-300"
            style={{ width: `${((step - 1) / 2) * 100}%` }}
          ></div>
        </div>
      </div>
      
      {/* Contedo do passo atual */}
      <form onSubmit={handleSubmit}>
        {renderStepContent()}
        
        {/* Botes de navegao */}
        <div className="flex justify-between mt-8">
          <button
            type="button"
            onClick={handlePrevStep}
            disabled={step === 1 || isLoading}
            className={`px-4 py-2 rounded-md text-sm font-medium ${
              step === 1
                ? 'bg-gray-100 text-gray-400 cursor-not-allowed'
                : 'bg-white border border-gray-300 text-gray-700 hover:bg-gray-50'
            }`}
          >
            Voltar
          </button>
          
          {step < 3 ? (
            <button
              type="button"
              onClick={handleNextStep}
              className="px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-md text-sm font-medium transition-colors"
            >
              Continuar
            </button>
          ) : (
            <button
              type="submit"
              disabled={isLoading}
              className="px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-md text-sm font-medium transition-colors flex items-center"
            >
              {isLoading ? (
                <>
                  <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                  Criando plano...
                </>
              ) : (
                'Criar Plano Personalizado'
              )}
            </button>
          )}
        </div>
      </form>
    </div>
  );
};

export default OnboardingForm; 