import React, { createContext, useContext, useState, ReactNode, useCallback, useEffect } from 'react';
import { 
  Plan, 
  UserSubscription, 
  AdReward, 
  PlanBenefit, 
  AdRewardType, 
  PlanType,
  MOCK_PLANS,
  MonetizationContextType
} from '../types';
import { v4 as uuidv4 } from 'uuid';

// Dados mock para desenvolvimento
const mockUserSubscription: UserSubscription = {
  id: 'sub_123456',
  user_id: 'user_123',
  plan_type: 'free',
  status: 'active',
  started_at: new Date('2023-01-01').toISOString(),
  expires_at: null,
  auto_renew: false,
  payment_method: null
};

const mockAdRewards: AdReward[] = [
  {
    id: 'reward_1',
    user_id: 'user_123',
    type: 'chat_messages',
    amount: 5,
    created_at: new Date('2023-05-15T10:30:00').toISOString(),
    expires_at: new Date('2023-05-15T23:59:59').toISOString(),
  },
  {
    id: 'reward_2',
    user_id: 'user_123',
    type: 'study_access',
    amount: 1,
    created_at: new Date('2023-05-14T15:45:00').toISOString(),
    expires_at: new Date('2023-05-14T23:59:59').toISOString(),
  }
];

// Criar o contexto com valor inicial
export const MonetizationContext = createContext<MonetizationContextType>({
  // Estado
  currentPlan: null,
  userSubscription: null,
  availablePlans: [],
  adRewards: [],
  isLoading: false,
  isCancelling: false,
  error: null,
  
  // Controle de limites
  chatMessagesRemaining: 0,
  studiesRemaining: 0,
  
  // Checar acesso
  isPremium: false,
  hasAccess: () => false,
  
  // Aes de controle
  fetchUserSubscription: async () => {},
  fetchAvailablePlans: async () => {},
  fetchAdRewards: async () => {},
  
  // Aes para pagamento
  startSubscription: async () => '',
  cancelSubscription: async () => {},
  
  // Aes para recompensa de anncios (plano Free)
  watchAdForReward: async () => {},
  useAdReward: async () => false,
  checkRewardAvailability: () => false,
  
  // Helpers
  refreshMonetizationData: async () => {},
  getRemainingTime: () => null
});

interface MonetizationProviderProps {
  children: ReactNode;
}

export const MonetizationProvider: React.FC<MonetizationProviderProps> = ({ children }) => {
  const [availablePlans, setAvailablePlans] = useState<Plan[]>([]);
  const [currentPlan, setCurrentPlan] = useState<Plan | null>(null);
  const [userSubscription, setUserSubscription] = useState<UserSubscription | null>(null);
  const [adRewards, setAdRewards] = useState<AdReward[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isCancelling, setIsCancelling] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [chatMessagesRemaining, setChatMessagesRemaining] = useState(0);
  const [studiesRemaining, setStudiesRemaining] = useState(0);

  // Propriedade computada que verifica se o usurio tem plano premium
  const isPremium = !!userSubscription && ['premium', 'premium_annual'].includes(userSubscription.plan_type);

  // Carregar dados iniciais
  useEffect(() => {
    refreshMonetizationData();
  }, []);

  // Atualizar limites com base no plano e recompensas
  useEffect(() => {
    if (!userSubscription || !availablePlans.length) return;
    
    // Encontrar o plano atual
    const plan = availablePlans.find(p => p.type === userSubscription.plan_type);
    if (plan) {
      setCurrentPlan(plan);
      
      // Definir limites padro baseados no plano
      if (plan.type === 'free' && plan.limits) {
        setChatMessagesRemaining(plan.limits.daily_chat_messages);
        setStudiesRemaining(plan.limits.monthly_studies);
      } else {
        // Planos premium tm acesso ilimitado
        setChatMessagesRemaining(999);
        setStudiesRemaining(999);
      }
    }

    // Adicionar recompensas aos limites
    const chatRewards = adRewards
      .filter(r => r.type === 'chat_messages')
      .reduce((sum, r) => sum + r.amount, 0);
    
    const studyRewards = adRewards
      .filter(r => r.type === 'study_access')
      .reduce((sum, r) => sum + r.amount, 0);
    
    if (plan?.type === 'free') {
      setChatMessagesRemaining(prev => prev + chatRewards);
      setStudiesRemaining(prev => prev + studyRewards);
    }
  }, [userSubscription, availablePlans, adRewards]);

  // Funes da API
  
  // Atualizar todos os dados
  const refreshMonetizationData = async (): Promise<void> => {
    try {
      setIsLoading(true);
      await Promise.all([
        fetchUserSubscription(),
        fetchAvailablePlans(),
        fetchAdRewards()
      ]);
    } catch (err) {
      setError('Erro ao carregar dados de monetizao.');
      console.error('Erro ao carregar dados de monetizao:', err);
    } finally {
      setIsLoading(false);
    }
  };

  // Buscar assinatura do usurio
  const fetchUserSubscription = async (): Promise<void> => {
    try {
      // Simular chamada API
      await new Promise(resolve => setTimeout(resolve, 500));
      setUserSubscription(mockUserSubscription);
    } catch (err) {
      setError('Erro ao buscar assinatura do usurio.');
      console.error('Erro ao buscar assinatura:', err);
    }
  };

  // Buscar planos disponveis
  const fetchAvailablePlans = async (): Promise<void> => {
    try {
      // Simular chamada API
      await new Promise(resolve => setTimeout(resolve, 500));
      setAvailablePlans(MOCK_PLANS);
    } catch (err) {
      setError('Erro ao buscar planos disponveis.');
      console.error('Erro ao buscar planos:', err);
    }
  };

  // Buscar recompensas de anncios
  const fetchAdRewards = async (): Promise<void> => {
    try {
      // Simular chamada API
      await new Promise(resolve => setTimeout(resolve, 500));
      setAdRewards(mockAdRewards);
    } catch (err) {
      setError('Erro ao buscar recompensas de anncios.');
      console.error('Erro ao buscar recompensas:', err);
    }
  };

  // Verificar se tem acesso a determinado benefcio
  const hasAccess = useCallback((feature: PlanBenefit): boolean => {
    if (!currentPlan) return false;
    return currentPlan.benefits.includes(feature);
  }, [currentPlan]);

  // Iniciar uma nova assinatura
  const startSubscription = async (planId: string): Promise<string> => {
    try {
      setIsLoading(true);
      
      // Encontrar o plano pelo ID
      const selectedPlan = availablePlans.find(p => p.type === planId);
      if (!selectedPlan) {
        throw new Error('Plano no encontrado');
      }
      
      // Simular chamada API
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // Para fins de desenvolvimento, simular a alterao da assinatura
      setUserSubscription({
        ...mockUserSubscription,
        plan_type: selectedPlan.type,
        status: 'active',
        started_at: new Date().toISOString(),
        expires_at: selectedPlan.type !== 'free' 
          ? new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString() 
          : null
      });
      
      return `/checkout/success?plan=${selectedPlan.type}`;
    } catch (err) {
      setError('Erro ao iniciar assinatura.');
      console.error('Erro ao iniciar assinatura:', err);
      return '/checkout/error';
    } finally {
      setIsLoading(false);
    }
  };

  // Cancelar uma assinatura existente
  const cancelSubscription = async (): Promise<void> => {
    try {
      setIsCancelling(true);
      // Simular chamada API
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // Para fins de desenvolvimento, voltar para o plano gratuito
      setUserSubscription({
        ...mockUserSubscription,
        plan_type: 'free',
        status: 'active',
        auto_renew: false
      });
    } catch (err) {
      setError('Erro ao cancelar assinatura.');
      console.error('Erro ao cancelar assinatura:', err);
    } finally {
      setIsCancelling(false);
    }
  };

  // Assistir um anncio para ganhar recompensa
  const watchAdForReward = async (rewardType: AdRewardType): Promise<void> => {
    try {
      // Simular chamada API - como se estivesse assistindo um anncio
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      // Adicionar nova recompensa
      const newReward: AdReward = {
        id: `reward_${Date.now()}`,
        user_id: 'user_123',
        type: rewardType,
        amount: rewardType === 'chat_messages' ? 5 : 1,
        created_at: new Date().toISOString(),
        expires_at: new Date(Date.now() + 24 * 60 * 60 * 1000).toISOString(),
      };
      
      setAdRewards(prev => [newReward, ...prev]);
    } catch (err) {
      setError('Erro ao processar recompensa de anncio.');
      console.error('Erro ao processar recompensa:', err);
    }
  };

  // Usar uma recompensa
  const useAdReward = async (rewardType: AdRewardType): Promise<boolean> => {
    try {
      // Encontrar uma recompensa vlida
      const rewardToUse = adRewards.find(reward => 
        reward.type === rewardType && 
        new Date(reward.expires_at) > new Date()
      );
      
      if (!rewardToUse) return false;
      
      // Simular chamada API
      await new Promise(resolve => setTimeout(resolve, 500));
      
      // Remover a recompensa usada
      setAdRewards(prev => prev.filter(reward => reward.id !== rewardToUse.id));
      
      return true;
    } catch (err) {
      setError('Erro ao usar recompensa.');
      console.error('Erro ao usar recompensa:', err);
      return false;
    }
  };

  // Verificar disponibilidade de recompensa
  const checkRewardAvailability = useCallback((rewardType: AdRewardType): boolean => {
    return adRewards.some(reward => 
      reward.type === rewardType && 
      new Date(reward.expires_at) > new Date()
    );
  }, [adRewards]);

  // Calcular tempo restante da assinatura
  const getRemainingTime = useCallback(() => {
    if (!userSubscription?.expires_at) return null;
    
    const expiresAt = new Date(userSubscription.expires_at);
    const now = new Date();
    const diffTime = expiresAt.getTime() - now.getTime();
    
    if (diffTime <= 0) return null;
    
    const days = Math.floor(diffTime / (1000 * 60 * 60 * 24));
    const hours = Math.floor((diffTime % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    
    return { days, hours };
  }, [userSubscription]);

  const contextValue: MonetizationContextType = {
    // Estado
    currentPlan,
    userSubscription,
    availablePlans,
    adRewards,
    isLoading,
    isCancelling,
    error,
    
    // Controle de limites
    chatMessagesRemaining,
    studiesRemaining,
    
    // Checar acesso
    isPremium,
    hasAccess,
    
    // Aes de controle
    fetchUserSubscription,
    fetchAvailablePlans,
    fetchAdRewards,
    
    // Aes para pagamento
    startSubscription,
    cancelSubscription,
    
    // Aes para recompensa de anncios (plano Free)
    watchAdForReward,
    useAdReward,
    checkRewardAvailability,
    
    // Helpers
    refreshMonetizationData,
    getRemainingTime
  };

  return (
    <MonetizationContext.Provider value={contextValue}>
      {children}
    </MonetizationContext.Provider>
  );
};

// Hook personalizado para usar o contexto
export const useMonetization = () => useContext(MonetizationContext); 