import React, { ReactNode } from 'react';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { ReactQueryDevtools } from '@tanstack/react-query-devtools';
import { ENV, FEATURES } from '../shared/constants/config';
import { AuthProvider } from '../features/auth/contexts/AuthContext';
import { StudyProvider } from '../features/study/contexts/StudyContext';
import { BibleProvider } from '../features/bible/contexts/BibleContext';
import { ChatProvider } from '../features/chat/contexts/ChatContext';
import { GamificationProvider } from '../features/gamification/contexts/GamificationContext';
import { MonetizationProvider } from '../features/monetization/contexts/MonetizationContext';

// Configuraes do QueryClient para diferentes tipos de dados
const defaultQueryConfig = {
  queries: {
    // Configurao padro para todas as queries
    staleTime: 1000 * 60 * 5, // 5 minutos
    cacheTime: 1000 * 60 * 30, // 30 minutos
    refetchOnWindowFocus: ENV.isDevelopment ? false : true,
    refetchOnReconnect: true,
    retry: 1,
    retryDelay: (attemptIndex: number) => Math.min(1000 * 2 ** attemptIndex, 30000),
  },
  mutations: {
    // Configurao padro para todas as mutations
    retry: 0,
    onError: (err: unknown) => {
      console.error('Mutation error:', err);
    },
  },
};

/**
 * Hook para criar uma instncia do QueryClient com configuraes otimizadas
 */
export const createQueryClient = () => {
  return new QueryClient({
    defaultOptions: defaultQueryConfig,
  });
};

// Criao do QueryClient
const queryClient = createQueryClient();

interface AppProvidersProps {
  children: ReactNode;
}

/**
 * Componente que configura todos os providers da aplicao
 */
export const AppProviders: React.FC<AppProvidersProps> = ({ children }) => {
  return (
    <QueryClientProvider client={queryClient}>
      <AuthProvider>
        <StudyProvider>
          <BibleProvider>
            <ChatProvider>
              <GamificationProvider>
                <MonetizationProvider>
                  {children}
                </MonetizationProvider>
              </GamificationProvider>
            </ChatProvider>
          </BibleProvider>
        </StudyProvider>
      </AuthProvider>
      {/* Mostra as devtools apenas em ambiente de desenvolvimento */}
      {ENV.isDevelopment && <ReactQueryDevtools initialIsOpen={false} position="bottom" />}
    </QueryClientProvider>
  );
};

export default AppProviders; 