import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useStudyContext } from '../contexts/StudyContext';
import { OnboardingPreferences, AIStudyPlanSuggestion } from '../types';
import OnboardingForm from '../components/OnboardingForm';

/**
 * Pgina de onboarding para coletar preferncias e criar plano personalizado
 */
const OnboardingPage: React.FC = () => {
  const navigate = useNavigate();
  const { submitOnboarding, confirmPersonalizedPlan, onboardingSuggestion, isMutating } = useStudyContext();
  const [showSuggestion, setShowSuggestion] = useState(false);

  // Lidar com o envio do formulrio
  const handleSubmitPreferences = async (preferences: OnboardingPreferences) => {
    try {
      await submitOnboarding(preferences);
      setShowSuggestion(true);
    } catch (error) {
      console.error('Erro ao enviar preferncias:', error);
    }
  };

  // Confirmar e criar o plano personalizado
  const handleConfirmPlan = () => {
    confirmPersonalizedPlan();
    navigate('/estudo/planos');
  };

  // Tela de exibio da sugesto de plano
  if (showSuggestion && onboardingSuggestion) {
    return (
      <div className="container mx-auto px-4 py-8 max-w-3xl">
        <h1 className="text-2xl font-bold text-gray-800 mb-6 text-center">
          Seu Plano Personalizado
        </h1>
        
        <div className="bg-white rounded-lg shadow-md p-6 mb-6">
          <h2 className="text-xl font-semibold mb-3">{onboardingSuggestion.title}</h2>
          <p className="text-gray-700 mb-4">{onboardingSuggestion.description}</p>
          
          <div className="border-t border-gray-200 pt-4 mt-4">
            <h3 className="font-medium text-gray-800 mb-2">Este plano inclui:</h3>
            <ul className="space-y-2">
              {onboardingSuggestion.sections.map((section, index) => (
                <li key={index} className="flex items-start">
                  <span className="flex-shrink-0 h-5 w-5 mr-2 text-blue-500">
                    <svg fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                    </svg>
                  </span>
                  <div>
                    <span className="font-medium">{section.title}</span>
                    <span className="text-sm text-gray-500 ml-2">({section.duration_minutes} minutos)</span>
                  </div>
                </li>
              ))}
            </ul>
          </div>
          
          <div className="border-t border-gray-200 pt-4 mt-4">
            <h3 className="font-medium text-gray-800 mb-2">Por que recomendamos este plano:</h3>
            <p className="text-gray-600">{onboardingSuggestion.justification}</p>
          </div>
          
          <div className="mt-8 flex justify-between">
            <button
              onClick={() => setShowSuggestion(false)}
              className="py-2 px-4 border border-gray-300 rounded-md text-gray-700 hover:bg-gray-50"
            >
              Voltar
            </button>
            <button
              onClick={handleConfirmPlan}
              disabled={isMutating}
              className="py-2 px-6 bg-blue-600 hover:bg-blue-700 text-white rounded-md flex items-center"
            >
              {isMutating ? (
                <>
                  <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                  Criando plano...
                </>
              ) : (
                'Comear este plano'
              )}
            </button>
          </div>
        </div>
      </div>
    );
  }

  // Formulrio de preferncias
  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-2xl font-bold text-gray-800 mb-6 text-center">
        Crie seu plano de estudo personalizado
      </h1>
      
      <div className="mb-6">
        <p className="text-center text-gray-600 max-w-xl mx-auto">
          Responda algumas perguntas para ajudarmos a criar um plano de estudo bblico personalizado
          de acordo com seus interesses e objetivos espirituais.
        </p>
      </div>
      
      <OnboardingForm
        onSubmit={handleSubmitPreferences}
        isLoading={isMutating}
      />
    </div>
  );
};

export default OnboardingPage; 