import React, { useState } from 'react';
import { useMonetizationContext } from '../providers/MonetizationProvider';
import PlanCard from '../components/PlanCard';
import SubscriptionInfo from '../components/SubscriptionInfo';
import UsageLimits from '../components/UsageLimits';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '../../../components/base/Tabs';
import { CreditCard, BarChart2, Settings } from 'lucide-react';

/**
 * Main monetization page - shows plans, subscription info, and usage limits
 */
export const MonetizationPage: React.FC = () => {
  const { plans, subscription, isLoading } = useMonetizationContext();

  if (isLoading) {
    return (
      <div className="container mx-auto py-8 px-4 sm:px-6 lg:px-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-4"></div>
          <div className="h-4 bg-gray-200 rounded w-2/4 mb-10"></div>
          <div className="h-80 bg-gray-200 rounded mb-6"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto py-8 px-4 sm:px-6 lg:px-8">
      {/* Page header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-800">Assinatura e Limites</h1>
        <p className="text-gray-600 mt-2">
          Gerencie sua assinatura, planos disponveis e limites de uso
        </p>
      </div>

      {/* Tabs for navigation */}
      <Tabs defaultValue="subscription" className="w-full">
        <TabsList className="mb-6 bg-white p-1 rounded-lg shadow">
          <TabsTrigger value="subscription" className="flex items-center">
            <CreditCard className="mr-2 h-5 w-5" />
            <span>Assinatura</span>
          </TabsTrigger>
          <TabsTrigger value="plans" className="flex items-center">
            <Settings className="mr-2 h-5 w-5" />
            <span>Planos</span>
          </TabsTrigger>
          <TabsTrigger value="usage" className="flex items-center">
            <BarChart2 className="mr-2 h-5 w-5" />
            <span>Limites de uso</span>
          </TabsTrigger>
        </TabsList>

        {/* Subscription tab content */}
        <TabsContent value="subscription">
          <div className="grid gap-6">
            <SubscriptionInfo />
            <UsageLimits />
          </div>
        </TabsContent>

        {/* Plans tab content */}
        <TabsContent value="plans">
          <div className="mb-6">
            <h2 className="text-xl font-semibold text-gray-800 mb-1">Planos disponveis</h2>
            <p className="text-gray-600">
              Escolha o plano que melhor atende s suas necessidades
            </p>
          </div>
          
          <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
            {plans.map(plan => (
              <PlanCard
                key={plan.id}
                plan={plan}
                isActive={subscription?.plan_id === plan.id}
              />
            ))}
          </div>
        </TabsContent>

        {/* Usage limits tab content */}
        <TabsContent value="usage">
          <UsageLimits />
        </TabsContent>
      </Tabs>
    </div>
  );
};

export default MonetizationPage; 