/**
 * Utilitrios para autenticao e gerenciamento de JWT
 */
import { authApi } from '../services/api';

// Token expiry buffer (2 minutes before actual expiry)
const TOKEN_EXPIRY_BUFFER_MS = 2 * 60 * 1000;

// Token refresh interval (check every minute)
const TOKEN_REFRESH_INTERVAL_MS = 60 * 1000;

// Store the refresh token timer
let refreshTokenTimer: number | null = null;

/**
 * Verifica se o JWT armazenado nos cookies est presente
 * Observao: Como estamos usando JWT em cookies HttpOnly, essa funo
 * apenas verifica se o usurio tem informaes no localStorage.
 * A validao real do token  feita pelo servidor.
 */
export function isAuthenticated(): boolean {
  const userData = localStorage.getItem('user');
  return !!userData; // Retorna true se existir dados de usurio no localStorage
}

/**
 * Armazena informaes bsicas do usurio no localStorage
 * @param user Dados do usurio aps autenticao
 * @param tokenExpiry Data de expirao do token (opcional)
 */
export function storeUserData(user: any, tokenExpiry?: number): void {
  if (!user) {
    console.warn('Tentativa de armazenar dados de usurio nulos');
    return;
  }
  
  // Armazena apenas informaes no sensveis (no o token)
  const userData = {
    id: user.id,
    name: user.name,
    email: user.email,
    role: user.role,
    preferences: user.preferences,
    lastLogin: new Date().toISOString(),
    // Store token expiry if provided (timestamp in milliseconds)
    tokenExpiry: tokenExpiry || null
  };
  
  localStorage.setItem('user', JSON.stringify(userData));
}

/**
 * Recupera os dados do usurio armazenados localmente
 * @returns Dados do usurio ou null
 */
export function getUserData(): any | null {
  const userData = localStorage.getItem('user');
  
  if (!userData) {
    return null;
  }
  
  try {
    return JSON.parse(userData);
  } catch (error) {
    console.error('Erro ao recuperar dados do usurio:', error);
    return null;
  }
}

/**
 * Recupera o ID do usurio logado
 * @returns ID do usurio ou null
 */
export function getUserId(): string | null {
  const userData = getUserData();
  return userData?.id || null;
}

/**
 * Verifica se o usurio tem uma determinada role
 * @param role Role a ser verificada
 * @returns True se o usurio tem a role especificada
 */
export function hasRole(role: string | string[]): boolean {
  const userData = getUserData();
  
  if (!userData || !userData.role) {
    return false;
  }
  
  if (Array.isArray(role)) {
    return role.includes(userData.role);
  }
  
  return userData.role === role;
}

/**
 * Verifica se o usurio  admin
 * @returns True se o usurio  admin
 */
export function isAdmin(): boolean {
  return hasRole(['admin', 'super_admin']);
}

/**
 * Remove todos os dados de autenticao
 */
export function clearAuth(): void {
  localStorage.removeItem('user');
  
  // Clear token refresh timer if exists
  if (refreshTokenTimer) {
    window.clearInterval(refreshTokenTimer);
    refreshTokenTimer = null;
  }
}

/**
 * Verifica se o token atual est prximo da expirao
 * @returns True se o token estiver prximo de expirar ou j expirado
 */
export function isTokenExpiring(): boolean {
  const userData = getUserData();
  
  if (!userData || !userData.tokenExpiry) {
    // Sem dados de expirao, assumir que est expirando
    return true;
  }
  
  const now = Date.now();
  const expiryTime = userData.tokenExpiry;
  
  // Verificar se o token expira em menos de 2 minutos (buffer)
  return now + TOKEN_EXPIRY_BUFFER_MS >= expiryTime;
}

/**
 * Calcula o tempo de expirao com base na resposta da API de autenticao
 * @param expiresIn Tempo de expirao em segundos
 * @returns Data de expirao em milissegundos (timestamp)
 */
export function calculateTokenExpiry(expiresIn: number): number {
  // expiresIn  o tempo em segundos at a expirao
  return Date.now() + (expiresIn * 1000);
}

/**
 * Inicia o monitoramento de expirao do token para renovao automtica
 * @param onRefreshSuccess Callback executado quando o token for renovado com sucesso
 * @param onRefreshFailed Callback executado quando o token falhar ao renovar
 */
export function startTokenRefreshMonitor(
  onRefreshSuccess: () => void,
  onRefreshFailed: () => void
): void {
  // Limpar timer anterior se existir
  if (refreshTokenTimer) {
    window.clearInterval(refreshTokenTimer);
  }
  
  // Criar timer para verificar a expirao periodicamente
  refreshTokenTimer = window.setInterval(async () => {
    try {
      if (isAuthenticated() && isTokenExpiring()) {
        // Token expirando, tentar renovar
        console.log('Token JWT prximo da expirao. Renovando...');
        
        const response = await authApi.post('/refresh');
        
        if (response.data && response.data.tokens) {
          const { user, tokens } = response.data;
          const expiryTime = calculateTokenExpiry(tokens.expires_in);
          
          // Atualizar dados do usurio com nova data de expirao
          storeUserData(user, expiryTime);
          
          if (onRefreshSuccess) {
            onRefreshSuccess();
          }
          
          console.log('Token JWT renovado com sucesso.');
        }
      }
    } catch (error) {
      console.error('Erro ao renovar o token JWT:', error);
      
      // Falha na renovao, deslogar usurio
      clearAuth();
      
      if (onRefreshFailed) {
        onRefreshFailed();
      }
    }
  }, TOKEN_REFRESH_INTERVAL_MS);
}

/**
 * Para o monitoramento de expirao do token
 */
export function stopTokenRefreshMonitor(): void {
  if (refreshTokenTimer) {
    window.clearInterval(refreshTokenTimer);
    refreshTokenTimer = null;
  }
}

/**
 * Decodifica um JWT sem validao (apenas para leitura do payload)
 * @param token JWT a ser decodificado
 * @returns Payload do JWT ou null se invlido
 */
export function decodeJWT(token: string): any | null {
  try {
    // Split the token into parts
    const parts = token.split('.');
    
    if (parts.length !== 3) {
      return null;
    }
    
    // Decode the payload (second part)
    const payload = parts[1];
    const decodedPayload = atob(payload.replace(/-/g, '+').replace(/_/g, '/'));
    
    return JSON.parse(decodedPayload);
  } catch (error) {
    console.error('Erro ao decodificar JWT:', error);
    return null;
  }
}

/**
 * Valida um email
 * @param email Email a ser validado
 * @returns True se o email for vlido
 */
export function isValidEmail(email: string): boolean {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
}

/**
 * Verifica se uma senha atende aos requisitos mnimos
 * @param password Senha a ser validada
 * @returns True se a senha atender aos requisitos
 */
export function isValidPassword(password: string): boolean {
  // Requisitos: mnimo 8 caracteres, ao menos 1 letra e 1 nmero
  return password.length >= 8 && 
         /[A-Za-z]/.test(password) && 
         /[0-9]/.test(password);
}

/**
 * Mensagens de erro para validao de senha
 * @param password Senha a ser validada
 * @returns Mensagem de erro ou null se a senha for vlida
 */
export function getPasswordValidationError(password: string): string | null {
  if (!password) return 'A senha  obrigatria';
  if (password.length < 8) return 'A senha deve ter no mnimo 8 caracteres';
  if (!/[A-Za-z]/.test(password)) return 'A senha deve conter pelo menos uma letra';
  if (!/[0-9]/.test(password)) return 'A senha deve conter pelo menos um nmero';
  
  return null;
}

/**
 * Hook para proteger rotas que requerem autenticao
 * (Usado em conjunto com React Router)
 */
export function requireAuth(): boolean {
  return isAuthenticated();
}

/**
 * Hook para proteger rotas que requerem role especfica
 * (Usado em conjunto com React Router)
 */
export function requireRole(role: string | string[]): boolean {
  return isAuthenticated() && hasRole(role);
}

export default {
  isAuthenticated,
  storeUserData,
  getUserData,
  getUserId,
  hasRole,
  isAdmin,
  clearAuth,
  isValidEmail,
  isValidPassword,
  getPasswordValidationError,
  requireAuth,
  requireRole,
  isTokenExpiring,
  calculateTokenExpiry,
  startTokenRefreshMonitor,
  stopTokenRefreshMonitor,
  decodeJWT,
}; 