import React from 'react';
import { format } from 'date-fns';
import { ptBR } from 'date-fns/locale';
import { CreditCard, Calendar, CheckCircle, XCircle, AlertTriangle } from 'lucide-react';
import { useMonetizationContext } from '../providers/MonetizationProvider';

/**
 * Component that displays the user's current subscription details
 */
export const SubscriptionInfo: React.FC = () => {
  const { subscription, currentPlan, cancelSubscription, isCancelingSubscription } = useMonetizationContext();

  if (!subscription || !currentPlan) {
    return (
      <div className="bg-white rounded-lg shadow p-6 text-center">
        <div className="animate-pulse">
          <div className="h-4 bg-gray-200 rounded w-3/4 mx-auto mb-4"></div>
          <div className="h-4 bg-gray-200 rounded w-1/2 mx-auto mb-4"></div>
          <div className="h-4 bg-gray-200 rounded w-2/3 mx-auto mb-4"></div>
        </div>
      </div>
    );
  }

  const formatDate = (dateString: Date) => {
    return format(new Date(dateString), "dd 'de' MMMM 'de' yyyy", { locale: ptBR });
  };

  const handleCancelSubscription = async () => {
    if (window.confirm('Tem certeza que deseja cancelar sua assinatura? Voc perder acesso aos recursos premium quando o perodo atual terminar.')) {
      try {
        await cancelSubscription();
      } catch (error) {
        console.error('Erro ao cancelar assinatura:', error);
      }
    }
  };

  return (
    <div className="bg-white rounded-lg shadow overflow-hidden">
      {/* Header with plan name */}
      <div className={`p-4 ${
        subscription.plan_type === 'free' 
          ? 'bg-gray-100' 
          : 'bg-blue-600 text-white'
      }`}>
        <div className="flex justify-between items-center">
          <h3 className={`font-semibold text-lg ${
            subscription.plan_type === 'free' ? 'text-gray-800' : 'text-white'
          }`}>
            {currentPlan.name}
          </h3>
          
          <div className={`px-3 py-1 rounded-full text-sm font-medium ${
            subscription.status === 'active' 
              ? 'bg-green-100 text-green-800' 
              : subscription.status === 'canceled' 
                ? 'bg-orange-100 text-orange-800'
                : 'bg-red-100 text-red-800'
          }`}>
            {subscription.status === 'active' 
              ? 'Ativo' 
              : subscription.status === 'canceled' 
                ? 'Cancelado'
                : subscription.status === 'expired'
                  ? 'Expirado'
                  : 'Pendente'
            }
          </div>
        </div>
      </div>
      
      {/* Subscription details */}
      <div className="p-4">
        <ul className="space-y-3">
          {/* Start date */}
          <li className="flex justify-between items-center py-2 border-b border-gray-100">
            <div className="flex items-center">
              <Calendar className="h-5 w-5 text-gray-500 mr-3" />
              <span className="text-gray-600">Data de incio</span>
            </div>
            <span className="text-gray-800 font-medium">{formatDate(subscription.start_date)}</span>
          </li>
          
          {/* Expiration date */}
          <li className="flex justify-between items-center py-2 border-b border-gray-100">
            <div className="flex items-center">
              <Calendar className="h-5 w-5 text-gray-500 mr-3" />
              <span className="text-gray-600">Validade</span>
            </div>
            <span className="text-gray-800 font-medium">{formatDate(subscription.expiration_date)}</span>
          </li>
          
          {/* Payment method */}
          {subscription.payment_method && (
            <li className="flex justify-between items-center py-2 border-b border-gray-100">
              <div className="flex items-center">
                <CreditCard className="h-5 w-5 text-gray-500 mr-3" />
                <span className="text-gray-600">Forma de pagamento</span>
              </div>
              <span className="text-gray-800 font-medium">{subscription.payment_method}</span>
            </li>
          )}
          
          {/* Auto-renew */}
          <li className="flex justify-between items-center py-2 border-b border-gray-100">
            <div className="flex items-center">
              {subscription.auto_renew ? (
                <CheckCircle className="h-5 w-5 text-green-500 mr-3" />
              ) : (
                <XCircle className="h-5 w-5 text-red-500 mr-3" />
              )}
              <span className="text-gray-600">Renovao automtica</span>
            </div>
            <span className={`font-medium ${
              subscription.auto_renew ? 'text-green-600' : 'text-red-600'
            }`}>
              {subscription.auto_renew ? 'Ativa' : 'Desativada'}
            </span>
          </li>
        </ul>
      </div>
      
      {/* Action buttons */}
      <div className="p-4 bg-gray-50 border-t border-gray-100">
        {subscription.status === 'active' && subscription.plan_type !== 'free' && (
          <div className="space-y-4">
            {subscription.auto_renew && (
              <div className="flex items-start mb-4 p-3 bg-yellow-50 border border-yellow-200 rounded-md">
                <AlertTriangle className="h-5 w-5 text-yellow-500 mr-2 flex-shrink-0 mt-0.5" />
                <p className="text-sm text-yellow-700">
                  Ao cancelar sua assinatura, voc continuar tendo acesso aos recursos premium at {formatDate(subscription.expiration_date)}. Aps essa data, seu plano ser revertido para o gratuito.
                </p>
              </div>
            )}
            
            <button
              onClick={handleCancelSubscription}
              className="w-full py-2 px-4 bg-white border border-red-300 text-red-600 rounded hover:bg-red-50 transition-colors duration-300"
              disabled={isCancelingSubscription}
            >
              {isCancelingSubscription ? 'Processando...' : 'Cancelar assinatura'}
            </button>
          </div>
        )}
        
        {(subscription.status === 'canceled' || subscription.status === 'expired') && (
          <div className="text-center text-gray-600 text-sm">
            Sua assinatura no est mais ativa. Considere fazer upgrade para voltar a ter acesso premium.
          </div>
        )}
      </div>
    </div>
  );
};

export default SubscriptionInfo; 