import React, { Component, ErrorInfo, ReactNode } from 'react';
import { errorHandler } from '../utils/errorHandler';

interface Props {
  children: ReactNode;
  fallback?: ReactNode;
}

interface State {
  hasError: boolean;
  error: Error | null;
}

/**
 * Componente que captura erros no tratados nas rvores de componentes abaixo
 * Evita que erros de um componente derrubem toda a aplicao
 */
class ErrorBoundary extends Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      hasError: false,
      error: null,
    };
  }

  /**
   * Atualiza o estado quando um erro  capturado
   */
  static getDerivedStateFromError(error: Error): State {
    return {
      hasError: true,
      error,
    };
  }

  /**
   * Registra o erro capturado
   */
  componentDidCatch(error: Error, errorInfo: ErrorInfo): void {
    // Registra o erro usando o errorHandler atualizado
    errorHandler.handleError(error, {
      source: 'ErrorBoundary',
      component: errorInfo.componentStack
    }, {
      showNotification: false, // No dispara notificaes, pois j estamos mostrando o fallback
    });
    
    console.error('Erro capturado pelo ErrorBoundary:', error);
    console.error('Informaes do componente:', errorInfo);
  }

  /**
   * Tenta recuperar a aplicao
   */
  handleReset = (): void => {
    this.setState({
      hasError: false,
      error: null,
    });
  };

  render(): ReactNode {
    const { children, fallback } = this.props;
    const { hasError, error } = this.state;

    if (hasError) {
      // Componente de fallback personalizado
      if (fallback) {
        return fallback;
      }

      // Fallback padro
      return (
        <div className="error-boundary">
          <div className="error-container">
            <h2>Ops! Algo deu errado</h2>
            <p>Ocorreu um erro inesperado na aplicao.</p>
            {error && (
              <details className="error-details">
                <summary>Detalhes do erro</summary>
                <p>{error.toString()}</p>
              </details>
            )}
            <div className="error-actions">
              <button onClick={this.handleReset}>Tentar novamente</button>
              <button onClick={() => window.location.reload()}>
                Recarregar pgina
              </button>
            </div>
          </div>
        </div>
      );
    }

    return children;
  }
}

export default ErrorBoundary; 