import React from 'react';
import { Navigate, useNavigate } from 'react-router-dom';
import { useAuthContext } from '../contexts/AuthContext';
import ForgotPasswordForm from '../components/ForgotPasswordForm';

/**
 * Pgina de recuperao de senha
 */
export const ForgotPasswordPage: React.FC = () => {
  const { isAuthenticated, isLoading } = useAuthContext();
  const navigate = useNavigate();
  
  // Funo chamada aps envio bem-sucedido do email de recuperao
  const handleSuccess = () => {
    // Opcional: poderia navegar para outra pgina ou mostrar mensagem de sucesso
    // neste caso, a prpria forma j mostra a mensagem de sucesso
  };
  
  // Se j estiver autenticado, redireciona
  if (isAuthenticated && !isLoading) {
    return <Navigate to="/home" replace />;
  }
  
  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md w-full space-y-8">
        <div>
          <h1 className="text-center text-3xl font-extrabold text-gray-900">
            Recuperar senha
          </h1>
          <p className="mt-2 text-center text-sm text-gray-600">
            Enviaremos instrues para o email cadastrado
          </p>
        </div>
        
        <ForgotPasswordForm onSuccess={handleSuccess} />
      </div>
    </div>
  );
};

export default ForgotPasswordPage; 