import { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import bibleService from '../api/bibleService';
import { Book, Chapter, Verse, SearchResultItem } from '../types';
import { BibleContextType } from '../contexts/BibleContext';

/**
 * Interface para estado da Bblia
 */
interface BibleState {
  selectedBook: Book | null;
  selectedChapter: Chapter | null;
  verses: Verse[];
  searchResults: SearchResultItem[];
  isLoading: boolean;
  isSearching: boolean;
  error: Error | null;
}

/**
 * Hook para gerenciar a interao com a Bblia
 * Utiliza React Query para gerenciar cache e estado
 */
export const useBible = (): BibleContextType => {
  const queryClient = useQueryClient();
  
  // Estado local para rastrear selees do usurio
  const [state, setState] = useState<BibleState>({
    selectedBook: null,
    selectedChapter: null,
    verses: [],
    searchResults: [],
    isLoading: false,
    isSearching: false,
    error: null
  });

  // Query para buscar lista de livros da Bblia
  const {
    data: books = [],
    isLoading: isLoadingBooks,
    error: booksError,
  } = useQuery({
    queryKey: ['books'],
    queryFn: () => bibleService.getBooks()
  });

  // Query para buscar captulos de um livro selecionado
  const {
    data: chapters = [],
    isLoading: isLoadingChapters,
    error: chaptersError,
    refetch: refetchChapters,
  } = useQuery({
    queryKey: ['chapters', state.selectedBook?.id],
    queryFn: () => {
      if (!state.selectedBook?.id) {
        return Promise.resolve([]);
      }
      return bibleService.getChapters(state.selectedBook.id);
    },
    enabled: !!state.selectedBook?.id,
  });

  // Query para buscar versculos de um captulo selecionado
  const {
    data: verses = [],
    isLoading: isLoadingVerses,
    error: versesError,
    refetch: refetchVerses,
  } = useQuery({
    queryKey: ['verses', state.selectedChapter?.id],
    queryFn: () => {
      if (!state.selectedChapter?.id) {
        return Promise.resolve([]);
      }
      return bibleService.getVerses(state.selectedChapter.id);
    },
    enabled: !!state.selectedChapter?.id,
  });

  // Busca na Bblia
  const search = async (query: string): Promise<SearchResultItem[]> => {
    if (!query || query.trim().length < 3) {
      setState(prev => ({ ...prev, searchResults: [], isSearching: false }));
      return [];
    }
    
    try {
      setState(prev => ({ ...prev, isSearching: true, error: null }));
      const results = await bibleService.search({ query });
      const searchItems = Array.isArray(results.items) ? results.items : [];
      setState(prev => ({ ...prev, searchResults: searchItems }));
      return searchItems;
    } catch (error) {
      setState(prev => ({ ...prev, error: error as Error, searchResults: [] }));
      return [];
    } finally {
      setState(prev => ({ ...prev, isSearching: false }));
    }
  };

  // Obter um versculo especfico
  const getSpecificVerse = async (bookId: number, chapterNumber: number, verseNumber: number): Promise<Verse> => {
    try {
      setState(prev => ({ ...prev, isLoading: true, error: null }));
      // Implementao simplificada - em produo voc buscaria o versculo especfico da API
      const chapters = await bibleService.getChapters(bookId);
      const chapter = chapters.find(c => c.number === chapterNumber);
      
      if (!chapter) throw new Error('Captulo no encontrado');
      
      const verses = await bibleService.getVerses(chapter.id);
      const verse = verses.find(v => v.number === verseNumber);
      
      if (!verse) throw new Error('Versculo no encontrado');
      
      return verse;
    } catch (error) {
      setState(prev => ({ ...prev, error: error as Error }));
      throw error;
    } finally {
      setState(prev => ({ ...prev, isLoading: false }));
    }
  };

  // Obter versculos por tema
  const getVersesByTheme = async (theme: string, count: number = 10): Promise<SearchResultItem[]> => {
    try {
      setState(prev => ({ ...prev, isSearching: true, error: null }));
      // Na implementao real, voc teria um endpoint especfico para temas
      // Aqui vamos simular usando a busca normal
      const results = await bibleService.search({ query: theme });
      const searchItems = Array.isArray(results.items) ? results.items.slice(0, count) : [];
      setState(prev => ({ ...prev, searchResults: searchItems }));
      return searchItems;
    } catch (error) {
      setState(prev => ({ ...prev, error: error as Error, searchResults: [] }));
      return [];
    } finally {
      setState(prev => ({ ...prev, isSearching: false }));
    }
  };

  // Aes
  const selectBook = (book: Book | null) => {
    setState(prev => ({ 
      ...prev, 
      selectedBook: book,
      selectedChapter: null,
      verses: []
    }));
    
    if (book) {
      refetchChapters();
    }
  };

  const selectChapter = (chapter: Chapter | null) => {
    setState(prev => ({ 
      ...prev, 
      selectedChapter: chapter,
      verses: []
    }));
    
    if (chapter) {
      refetchVerses();
    }
  };

  const clearSelection = () => {
    setState(prev => ({
      ...prev,
      selectedBook: null,
      selectedChapter: null,
      verses: []
    }));
  };

  return {
    // Estados
    books: books || [],
    chapters: chapters || [],
    verses: verses || [],
    selectedBook: state.selectedBook,
    selectedChapter: state.selectedChapter,
    searchResults: state.searchResults,
    isLoading: isLoadingBooks || isLoadingChapters || isLoadingVerses || state.isLoading,
    error: booksError || chaptersError || versesError || state.error,
    
    // Estados especficos de loading
    isLoadingBooks,
    isLoadingChapters,
    isLoadingVerses,
    isSearching: state.isSearching,
    
    // Aes
    selectBook,
    selectChapter,
    clearSelection,
    search,
    getSpecificVerse,
    getVersesByTheme
  };
}; 