import React, { createContext, useContext, ReactNode } from 'react';
import { 
  StudyContextType
} from '../types';
import useToast from '../../../shared/hooks/useToast';
import useStudyQuery from '../hooks/useStudyQuery';

// Contexto de Estudo
const StudyContext = createContext<StudyContextType | null>(null);

// Interface de props do provedor
interface StudyProviderProps {
  children: ReactNode;
}

// Interface temporria para onboarding
interface OnboardingFormData {
  objectives: string[];
  bible_experience_level: string;
  content_preferences: string[];
  preferred_time: string;
}

/**
 * Provedor do contexto de estudo
 * Encapsula a lgica de React Query e oferece uma API limpa 
 * para componentes usarem
 */
export const StudyProvider: React.FC<StudyProviderProps> = ({ children }) => {
  const toast = useToast();
  
  // Obter dados e funcionalidades via React Query
  const studyQuery = useStudyQuery();

  // Adaptador para manter compatibilidade com o cdigo existente que espera OnboardingFormData
  const createPersonalizedPlan = async (data: OnboardingFormData) => {
    // Convertendo OnboardingFormData para OnboardingPreferences
    const preferences = {
      objectives: data.objectives,
      bible_experience_level: data.bible_experience_level,
      content_preferences: data.content_preferences,
      preferred_time: data.preferred_time
    };
    
    return studyQuery.createPersonalizedPlan(preferences);
  };
  
  // Fornecer contexto para toda a rvore de componentes
  return (
    <StudyContext.Provider value={studyQuery}>
      {children}
    </StudyContext.Provider>
  );
};

/**
 * Hook para consumir o contexto de estudo
 * @returns Contexto de estudo com todos os dados e funes
 */
export const useStudyContext = (): StudyContextType => {
  const context = useContext(StudyContext);
  
  if (!context) {
    throw new Error('useStudyContext deve ser usado dentro de um StudyProvider');
  }
  
  return context;
};

export default StudyContext; 