import { API_URLS } from '../../../shared/constants/config';
import { createApiClient, authApi } from '../../../shared/services/api';
import {
  User,
  LoginCredentials,
  RegisterData,
  ForgotPasswordData,
  ResetPasswordData,
  ProfileUpdateData,
  AuthResponse,
  UserPreferences
} from '../types';

// Usar a instncia pr-configurada para o microsservio de autenticao
// const authClient = createApiClient({ baseURL: API_URLS.auth });

/**
 * Servio para operaes relacionadas  autenticao
 */
const authService = {
  /**
   * Obter dados do usurio atual
   */
  getUser: async (): Promise<User> => {
    const response = await authApi.get<User>('/user');
    return response.data;
  },

  /**
   * Realizar login com email e senha
   */
  login: async (credentials: LoginCredentials): Promise<AuthResponse> => {
    const response = await authApi.post<AuthResponse>('/login', credentials);
    return response.data;
  },

  /**
   * Registrar novo usurio
   */
  register: async (data: RegisterData): Promise<AuthResponse> => {
    const response = await authApi.post<AuthResponse>('/register', data);
    return response.data;
  },

  /**
   * Realizar logout
   */
  logout: async (): Promise<void> => {
    await authApi.post<void>('/logout');
  },

  /**
   * Solicitar recuperao de senha
   */
  forgotPassword: async (data: ForgotPasswordData): Promise<void> => {
    await authApi.post<void>('/forgot-password', data);
  },

  /**
   * Redefinir senha com token
   */
  resetPassword: async (data: ResetPasswordData): Promise<void> => {
    await authApi.post<void>('/reset-password', data);
  },

  /**
   * Renovar token de acesso
   */
  refreshToken: async (): Promise<AuthResponse> => {
    const response = await authApi.post<AuthResponse>('/refresh');
    return response.data;
  },

  /**
   * Atualizar dados do perfil
   */
  updateProfile: async (data: ProfileUpdateData): Promise<User> => {
    // Se tiver um arquivo de avatar, use FormData
    if (data.avatar) {
      const formData = new FormData();
      
      // Adicionar apenas os campos com valores
      if (data.name) formData.append('name', data.name);
      if (data.email) formData.append('email', data.email);
      if (data.current_password) formData.append('current_password', data.current_password);
      if (data.password) formData.append('password', data.password);
      if (data.password_confirmation) formData.append('password_confirmation', data.password_confirmation);
      
      // Adicionar avatar
      formData.append('avatar', data.avatar);
      
      const response = await authApi.post<User>('/profile', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      return response.data;
    }
    
    // Se no tiver avatar, use JSON normal
    const response = await authApi.post<User>('/profile', data);
    return response.data;
  },

  /**
   * Verificar token de email
   */
  verifyEmail: async (token: string): Promise<void> => {
    await authApi.post<void>(`/verify-email/${token}`);
  },
  
  /**
   * Salvar preferncias do usurio (onboarding)
   */
  savePreferences: async (preferences: UserPreferences): Promise<void> => {
    await authApi.post<void>('/preferences', preferences);
  },
};

export default authService; 