import api from '../../../shared/services/api';
import { API_URLS } from '../../../shared/constants/config';
import { 
  Book, 
  Chapter, 
  Verse, 
  SearchResult, 
  BooksResponse, 
  ChaptersResponse, 
  VersesResponse, 
  SearchParams, 
  SearchResponse 
} from '../types';

/**
 * Servio para operaes relacionadas  Bblia
 * Implementao do item 10.5.1 - Integrao com MS-Bible
 */
const bibleService = {
  /**
   * Obtm a lista de todos os livros da Bblia
   * Endpoint: GET /api/bible/books
   */
  getBooks: async (testament?: 'old' | 'new'): Promise<Book[]> => {
    const params = testament ? { testament } : {};
    const response = await api.bibleApi.get<BooksResponse>('/books', { params });
    return response.data.books;
  },

  /**
   * Obtm um livro especfico da Bblia pelo ID
   * Endpoint: GET /api/bible/books/:id
   */
  getBook: async (bookId: number): Promise<Book> => {
    const response = await api.bibleApi.get<Book>(`/books/${bookId}`);
    return response.data;
  },

  /**
   * Obtm todos os captulos de um livro especfico
   * Endpoint: GET /api/bible/books/:id/chapters
   */
  getChapters: async (bookId: number): Promise<Chapter[]> => {
    const response = await api.bibleApi.get<ChaptersResponse>(`/books/${bookId}/chapters`);
    return response.data.chapters;
  },

  /**
   * Obtm um captulo especfico pelo ID
   * Endpoint: GET /api/bible/chapters/:id
   */
  getChapter: async (chapterId: number): Promise<Chapter> => {
    const response = await api.bibleApi.get<Chapter>(`/chapters/${chapterId}`);
    return response.data;
  },

  /**
   * Obtm os versculos de um captulo especfico
   * Endpoint: GET /api/bible/chapters/:id/verses
   */
  getVerses: async (chapterId: number): Promise<Verse[]> => {
    const response = await api.bibleApi.get<VersesResponse>(`/chapters/${chapterId}/verses`);
    return response.data.verses;
  },

  /**
   * Obtm um versculo especfico pelo ID do captulo e nmero do versculo
   * Endpoint: GET /api/bible/chapters/:chapterId/verses/:verseNumber
   */
  getVerse: async (chapterId: number, verseNumber: number): Promise<Verse> => {
    const response = await api.bibleApi.get<Verse>(`/chapters/${chapterId}/verses/${verseNumber}`);
    return response.data;
  },

  /**
   * Pesquisa na Bblia por texto ou referncia
   * Endpoint: GET /api/bible/search
   */
  search: async (params: SearchParams): Promise<SearchResult> => {
    const response = await api.bibleApi.get<SearchResponse>('/search', { params });
    return response.data.results;
  },

  /**
   * Obtm versculos por tema ou categoria
   * Endpoint: GET /api/bible/themes/:themeId
   */
  getVersesByTheme: async (themeId: string): Promise<Verse[]> => {
    const response = await api.bibleApi.get<VersesResponse>(`/themes/${themeId}`);
    return response.data.verses;
  },

  /**
   * Obtm um versculo aleatrio, opcionalmente filtrado por tema
   * Endpoint: GET /api/bible/random-verse
   */
  getRandomVerse: async (themeId?: string): Promise<Verse> => {
    const params = themeId ? { theme: themeId } : {};
    const response = await api.bibleApi.get<Verse>('/random-verse', { params });
    return response.data;
  },

  /**
   * Obtm versculos do dia
   * Endpoint: GET /api/bible/verse-of-day
   */
  getVerseOfDay: async (): Promise<Verse> => {
    const response = await api.bibleApi.get<Verse>('/verse-of-day');
    return response.data;
  }
};

export default bibleService; 