import React, { createContext, ReactNode, useContext, useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  User, 
  LoginCredentials, 
  RegisterData, 
  ForgotPasswordData, 
  ResetPasswordData, 
  ProfileUpdateData,
  AuthResponse,
  UserPreferences
} from '../types';
import { useToast } from '../../../shared/hooks/useToast';
import { useQueryClient } from '@tanstack/react-query';
import authService from '../api/authService';
import {
  isAuthenticated,
  storeUserData,
  getUserData,
  clearAuth,
  calculateTokenExpiry,
  startTokenRefreshMonitor,
  stopTokenRefreshMonitor,
  isTokenExpiring
} from '../../../shared/utils/auth';

// Interface para o valor do contexto de autenticao
export interface AuthContextValue {
  // Estado
  user: User | null;
  isAuthenticated: boolean;
  isLoading: boolean;
  error: string | null;
  
  // Mtodos de autenticao
  login: (credentials: LoginCredentials) => Promise<AuthResponse>;
  register: (data: RegisterData) => Promise<AuthResponse>;
  logout: () => Promise<void>;
  updateProfile: (data: ProfileUpdateData) => Promise<User>;
  forgotPassword: (data: ForgotPasswordData) => Promise<void>;
  resetPassword: (data: ResetPasswordData) => Promise<void>;
  verifyEmail: (token: string) => Promise<void>;
  refreshToken: () => Promise<boolean>;
  savePreferences: (preferences: UserPreferences) => Promise<boolean>; 
  
  // Estados de loading
  isLoginLoading: boolean;
  isRegisterLoading: boolean;
  isLogoutLoading: boolean;
  isUpdatingProfile: boolean;
  isForgotPasswordLoading: boolean;
  isResetPasswordLoading: boolean;
  isResetingPassword: boolean;
  isSavingPreferences: boolean;
  
  // Recarregar dados
  refetchUser: () => Promise<User | null>;
}

// Criar o contexto
const AuthContext = createContext<AuthContextValue | undefined>(undefined);

// Props para o provedor do contexto
interface AuthProviderProps {
  children: ReactNode;
}

/**
 * Provedor do contexto de autenticao
 */
export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  // States para o loading de cada operao
  const [isLoginLoading, setIsLoginLoading] = useState(false);
  const [isRegisterLoading, setIsRegisterLoading] = useState(false);
  const [isLogoutLoading, setIsLogoutLoading] = useState(false);
  const [isUpdatingProfile, setIsUpdatingProfile] = useState(false);
  const [isForgotPasswordLoading, setIsForgotPasswordLoading] = useState(false);
  const [isResetPasswordLoading, setIsResetPasswordLoading] = useState(false);
  const [isResetingPassword, setIsResetingPassword] = useState(false);
  const [isSavingPreferences, setIsSavingPreferences] = useState(false);

  const navigate = useNavigate();
  const toast = useToast();
  const queryClient = useQueryClient();

  // Funo para carregar dados do usurio do servidor
  const loadUserData = useCallback(async () => {
    if (!isAuthenticated()) {
      setLoading(false);
      return null;
    }

    try {
      setLoading(true);
      
      // Usar o servio real para obter dados do usurio
      const userData = await authService.getUser();
      setUser(userData);
      
      // Atualizar o cache do React Query para este usurio
      queryClient.setQueryData(['currentUser'], userData);
      
      return userData;
    } catch (err: any) {
      console.error('Erro ao carregar dados do usurio:', err);
      
      // Verificar tipos especficos de erro de autenticao
      if (err.response?.status === 401) {
        // Token invlido ou expirado
        if (isTokenExpiring()) {
          try {
            // Tenta renovar o token automaticamente
            const success = await refreshToken();
            if (success) {
              // Se o refresh teve sucesso, tenta carregar o usurio novamente
              return loadUserData();
            }
          } catch (refreshErr) {
            // Falha ao renovar o token, limpar autenticao
            clearAuth();
            setUser(null);
            toast.error('Sua sesso expirou. Por favor, faa login novamente.');
            navigate('/login?expired=true');
          }
        } else {
          // Outro erro de autenticao
          clearAuth();
          setUser(null);
          toast.error('Erro de autenticao. Por favor, faa login novamente.');
        }
      } else if (err.response?.status === 403) {
        // Usurio no autorizado
        toast.error('Voc no tem permisso para acessar este recurso.');
      } else {
        // Outros erros
        toast.error('Ocorreu um erro ao carregar seus dados. Tente novamente mais tarde.');
      }
      
      return null;
    } finally {
      setLoading(false);
    }
  }, [navigate, toast, queryClient]);

  // Funo de callback para quando o token for renovado
  const handleTokenRefreshSuccess = useCallback(() => {
    // Recarregar dados do usurio aps refresh para garantir dados atualizados
    loadUserData();
    toast.info('Sua sesso foi renovada automaticamente', { autoClose: 2000 });
  }, [loadUserData, toast]);

  // Funo de callback para quando o refresh falhar
  const handleTokenRefreshFailed = useCallback(() => {
    clearAuth();
    setUser(null);
    
    // Invalidar todos os queries relacionados ao usurio
    queryClient.invalidateQueries({ queryKey: ['currentUser'] });
    
    toast.error('Sua sesso expirou. Por favor, faa login novamente.');
    navigate('/login?expired=true');
  }, [navigate, toast, queryClient]);

  // Verificar se o usurio est autenticado ao iniciar
  useEffect(() => {
    const initAuth = async () => {
      if (isAuthenticated()) {
        try {
          // Carregar dados do usurio e iniciar monitoramento do token
          const userData = await loadUserData();
          
          if (userData) {
            // Iniciar monitoramento de refresh do token
            startTokenRefreshMonitor(
              handleTokenRefreshSuccess,
              handleTokenRefreshFailed
            );
          } else {
            // Se no conseguir carregar dados, provavelmente o token  invlido
            clearAuth();
            navigate('/login?session=expired');
          }
        } catch (err) {
          clearAuth();
          setUser(null);
          console.error('Erro na inicializao da autenticao:', err);
        }
      } else {
        setLoading(false);
      }
    };

    initAuth();

    // Cleanup: parar monitoramento quando o componente for desmontado
    return () => {
      stopTokenRefreshMonitor();
    };
  }, [loadUserData, handleTokenRefreshSuccess, handleTokenRefreshFailed, navigate]);

  // Funo de login
  const login = async (credentials: LoginCredentials): Promise<AuthResponse> => {
    setIsLoginLoading(true);
    setError(null);

    try {
      const authResponse = await authService.login(credentials);
      
      // Calcular tempo de expirao do token
      const tokenExpiry = calculateTokenExpiry(authResponse.tokens.expires_in);
      
      // Armazenar dados com tempo de expirao
      storeUserData(authResponse.user, tokenExpiry);
      
      // Atualizar estado do usurio
      setUser(authResponse.user);
      
      // Atualizar o cache do React Query
      queryClient.setQueryData(['currentUser'], authResponse.user);
      
      // Iniciar monitoramento de refresh do token
      startTokenRefreshMonitor(
        handleTokenRefreshSuccess,
        handleTokenRefreshFailed
      );
      
      toast.success(`Bem-vindo de volta, ${authResponse.user.name}!`);
      return authResponse;
    } catch (err: any) {
      // Tratamento especfico de erros de autenticao
      if (err.response?.status === 401) {
        const errorMessage = 'Credenciais invlidas. Verifique seu email e senha.';
        setError(errorMessage);
        toast.error(errorMessage);
      } else if (err.response?.status === 403) {
        const errorMessage = 'Sua conta est bloqueada. Entre em contato com o suporte.';
        setError(errorMessage);
        toast.error(errorMessage);
      } else if (err.response?.status === 429) {
        const errorMessage = 'Muitas tentativas de login. Tente novamente em alguns minutos.';
        setError(errorMessage);
        toast.error(errorMessage);
      } else {
        const errorMessage = err.response?.data?.message || 
                             err.message || 
                             'Erro ao fazer login. Verifique suas credenciais.';
        setError(errorMessage);
        toast.error(errorMessage);
      }
      throw err;
    } finally {
      setIsLoginLoading(false);
    }
  };

  // Funo de registro
  const register = async (data: RegisterData): Promise<AuthResponse> => {
    setIsRegisterLoading(true);
    setError(null);

    try {
      const authResponse = await authService.register(data);
      
      // Calcular tempo de expirao do token
      const tokenExpiry = calculateTokenExpiry(authResponse.tokens.expires_in);
      
      // Armazenar dados com tempo de expirao
      storeUserData(authResponse.user, tokenExpiry);
      
      // Atualizar estado do usurio
      setUser(authResponse.user);
      
      // Atualizar o cache do React Query
      queryClient.setQueryData(['currentUser'], authResponse.user);
      
      // Iniciar monitoramento de refresh do token
      startTokenRefreshMonitor(
        handleTokenRefreshSuccess,
        handleTokenRefreshFailed
      );
      
      toast.success(`Conta criada com sucesso! Bem-vindo, ${authResponse.user.name}!`);
      return authResponse;
    } catch (err: any) {
      // Tratamento especfico de erros de registro
      if (err.response?.status === 409) {
        const errorMessage = 'Este e-mail j est em uso. Tente outro ou recupere sua senha.';
        setError(errorMessage);
        toast.error(errorMessage);
      } else if (err.response?.status === 400) {
        const errorMessage = err.response?.data?.message || 'Dados invlidos. Verifique as informaes fornecidas.';
        setError(errorMessage);
        toast.error(errorMessage);
      } else {
        const errorMessage = err.response?.data?.message || 
                            err.message || 
                            'Erro ao registrar. Verifique os dados informados.';
        setError(errorMessage);
        toast.error(errorMessage);
      }
      throw err;
    } finally {
      setIsRegisterLoading(false);
    }
  };

  // Funo de logout
  const logout = async (): Promise<void> => {
    setIsLogoutLoading(true);
    
    try {
      // Parar monitoramento do token
      stopTokenRefreshMonitor();
      
      // Chamar endpoint de logout
      await authService.logout();
      
      // Limpar dados locais
      clearAuth();
      setUser(null);
      
      // Invalidar todos os queries do React Query relacionados ao usurio
      queryClient.invalidateQueries({ queryKey: ['currentUser'] });
      queryClient.invalidateQueries({ queryKey: ['userPreferences'] });
      queryClient.invalidateQueries({ queryKey: ['userStudies'] });
      
      toast.success('Logout realizado com sucesso!');
      
      // Redirecionar para login
      navigate('/login');
    } catch (err: any) {
      console.error('Erro ao fazer logout:', err);
      
      // Mesmo com erro, limpar dados locais
      clearAuth();
      setUser(null);
      
      // Invalidar queries do React Query
      queryClient.invalidateQueries({ queryKey: ['currentUser'] });
      
      // Redirecionar para login
      navigate('/login');
    } finally {
      setIsLogoutLoading(false);
    }
  };

  // Funo para atualizar perfil
  const updateProfile = async (data: ProfileUpdateData): Promise<User> => {
    setIsUpdatingProfile(true);
    
    try {
      const updatedUser = await authService.updateProfile(data);
      
      // Atualizar dados do usurio mantendo a expirao
      const userData = getUserData();
      storeUserData(updatedUser, userData?.tokenExpiry);
      
      setUser(updatedUser);
      
      // Atualizar o cache do React Query
      queryClient.setQueryData(['currentUser'], updatedUser);
      
      toast.success('Perfil atualizado com sucesso!');
      
      return updatedUser;
    } catch (err: any) {
      // Tratamento especfico de erros de atualizao de perfil
      if (err.response?.status === 400) {
        const errorMessage = err.response?.data?.message || 'Dados invlidos. Verifique as informaes fornecidas.';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      } else if (err.response?.status === 401) {
        const errorMessage = 'Sua sesso expirou. Por favor, faa login novamente.';
        toast.error(errorMessage);
        navigate('/login?expired=true');
        throw new Error(errorMessage);
      } else if (err.response?.status === 409) {
        const errorMessage = 'Este e-mail j est em uso por outra conta.';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      } else {
        const errorMessage = err.response?.data?.message || 
                            err.message || 
                            'Erro ao atualizar perfil';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      }
    } finally {
      setIsUpdatingProfile(false);
    }
  };

  // Funo para recuperao de senha
  const forgotPassword = async (data: ForgotPasswordData): Promise<void> => {
    setIsForgotPasswordLoading(true);
    
    try {
      await authService.forgotPassword(data);
      toast.success('Instrues de recuperao enviadas para seu e-mail!');
    } catch (err: any) {
      // Tratamento especfico de erros de recuperao de senha
      if (err.response?.status === 404) {
        const errorMessage = 'E-mail no encontrado em nosso sistema.';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      } else if (err.response?.status === 429) {
        const errorMessage = 'Muitas solicitaes. Aguarde alguns minutos antes de tentar novamente.';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      } else {
        const errorMessage = err.response?.data?.message || 
                            err.message || 
                            'Erro ao solicitar recuperao de senha';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      }
    } finally {
      setIsForgotPasswordLoading(false);
    }
  };

  // Funo para redefinir senha
  const resetPassword = async (data: ResetPasswordData): Promise<void> => {
    setIsResetPasswordLoading(true);
    setIsResetingPassword(true);
    
    try {
      await authService.resetPassword(data);
      toast.success('Senha redefinida com sucesso! Faa login com sua nova senha.');
      navigate('/login');
    } catch (err: any) {
      // Tratamento especfico de erros de redefinio de senha
      if (err.response?.status === 400) {
        const errorMessage = err.response?.data?.message || 'Token invlido ou dados incorretos.';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      } else if (err.response?.status === 401) {
        const errorMessage = 'O link de redefinio de senha expirou. Solicite um novo.';
        toast.error(errorMessage);
        navigate('/forgot-password');
        throw new Error(errorMessage);
      } else {
        const errorMessage = err.response?.data?.message || 
                            err.message || 
                            'Erro ao redefinir senha. O link pode ter expirado.';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      }
    } finally {
      setIsResetPasswordLoading(false);
      setIsResetingPassword(false);
    }
  };

  // Funo para verificar email
  const verifyEmail = async (token: string): Promise<void> => {
    try {
      await authService.verifyEmail(token);
      toast.success('E-mail verificado com sucesso!');
      
      // Recarregar dados do usurio para atualizar status de verificao
      await loadUserData();
    } catch (err: any) {
      // Tratamento especfico de erros de verificao de email
      if (err.response?.status === 400) {
        const errorMessage = 'Token de verificao invlido.';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      } else if (err.response?.status === 401) {
        const errorMessage = 'O link de verificao expirou. Solicite um novo em seu perfil.';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      } else {
        const errorMessage = err.response?.data?.message || 
                            err.message || 
                            'Erro ao verificar e-mail. O link pode ter expirado.';
        toast.error(errorMessage);
        throw new Error(errorMessage);
      }
    }
  };

  // Funo para salvar preferncias do onboarding
  const savePreferences = async (preferences: UserPreferences): Promise<boolean> => {
    setIsSavingPreferences(true);
    
    try {
      await authService.savePreferences(preferences);
      
      // Recarregar dados do usurio para ter preferncias atualizadas
      const updatedUser = await loadUserData();
      
      if (updatedUser) {
        // Atualizar o cache do React Query
        queryClient.setQueryData(['userPreferences'], preferences);
        
        toast.success('Preferncias salvas com sucesso!');
        return true;
      }
      
      return false;
    } catch (err: any) {
      // Tratamento especfico de erros de salvamento de preferncias
      if (err.response?.status === 401) {
        const errorMessage = 'Sua sesso expirou. Por favor, faa login novamente.';
        toast.error(errorMessage);
        navigate('/login?expired=true');
        return false;
      } else if (err.response?.status === 400) {
        const errorMessage = err.response?.data?.message || 'Dados invlidos. Verifique as preferncias fornecidas.';
        toast.error(errorMessage);
        return false;
      } else {
        const errorMessage = err.response?.data?.message || 
                            err.message || 
                            'Erro ao salvar preferncias';
        toast.error(errorMessage);
        return false;
      }
    } finally {
      setIsSavingPreferences(false);
    }
  };

  // Funo para renovar token manualmente
  const refreshToken = async (): Promise<boolean> => {
    try {
      const authResponse = await authService.refreshToken();
      
      // Calcular novo tempo de expirao
      const tokenExpiry = calculateTokenExpiry(authResponse.tokens.expires_in);
      
      // Atualizar dados do usurio com nova expirao
      storeUserData(authResponse.user, tokenExpiry);
      
      // Atualizar estado
      setUser(authResponse.user);
      
      // Atualizar cache do React Query
      queryClient.setQueryData(['currentUser'], authResponse.user);
      
      return true;
    } catch (err: any) {
      console.error('Erro ao renovar token:', err);
      
      // Tratamento especfico de erros de refresh token
      if (err.response?.status === 401) {
        // Falha crtica de autenticao, deslogar
        clearAuth();
        setUser(null);
        
        // Invalidar queries
        queryClient.invalidateQueries({ queryKey: ['currentUser'] });
        
        navigate('/login?expired=true');
      }
      
      return false;
    }
  };

  // Recarregar dados do usurio
  const refetchUser = async (): Promise<User | null> => {
    return loadUserData();
  };

  const value: AuthContextValue = {
    user,
    isAuthenticated: !!user,
    isLoading: loading,
    error,
    login,
    register,
    logout,
    updateProfile,
    forgotPassword,
    resetPassword,
    verifyEmail,
    refreshToken,
    refetchUser,
    savePreferences,
    isLoginLoading,
    isRegisterLoading,
    isLogoutLoading,
    isUpdatingProfile,
    isForgotPasswordLoading,
    isResetPasswordLoading,
    isResetingPassword,
    isSavingPreferences,
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

/**
 * Hook para acessar o contexto de autenticao
 */
export const useAuthContext = () => {
  const context = useContext(AuthContext);
  
  if (context === undefined) {
    throw new Error('useAuthContext deve ser usado dentro de um AuthProvider');
  }
  
  return context;
};

export default AuthContext; 