import React, { useEffect, lazy, Suspense } from 'react';
import { RouterProvider, createBrowserRouter, Navigate, Outlet } from 'react-router-dom';
import { Toaster } from 'react-hot-toast';
import ErrorBoundary from '../shared/components/ErrorBoundary';
import { errorHandler, AppError } from '../shared/utils/errorHandler';
import LoadingPage from '../shared/components/LoadingPage';

// Pages
import LoginPage from '../features/auth/pages/LoginPage';
import RegisterPage from '../features/auth/pages/RegisterPage';
import ForgotPasswordPage from '../features/auth/pages/ForgotPasswordPage';
import AuthOnboardingPage from '../pages/Onboarding';
import HomePage from '../pages/Home';
import MainLayout from '../layouts/MainLayout';
import BibleExplorerPage from '../features/bible/pages/BibleExplorerPage';
import ChatPage from '../features/chat/pages/ChatPage';
import { StudyPlansPage, OnboardingPage as StudyOnboardingPage } from '../features/study';
import SettingsPage from '../pages/Settings';
import NotFoundPage from '../pages/NotFound';
import { GamificationPage } from '../features/gamification';
import MonetizationPage from '../features/monetization/pages/MonetizationPage';

// Auth components
import ProtectedLayout from '../features/auth/components/ProtectedRoute';

/**
 * Componente raiz da aplicao
 * Configura rotas, contextos globais e tratamento de erros
 */
const App: React.FC = () => {
  useEffect(() => {
    // Registra handler global para erros no capturados
    const handleUnhandledError = (error: AppError) => {
      console.error('Erro no capturado:', error);
      // Aqui poderia exibir uma notificao toast ou modal
    };
    
    // Registra o callback para ser notificado de erros
    errorHandler.registerErrorCallback(handleUnhandledError);
    
    // Limpa o callback quando o componente for desmontado
    return () => {
      errorHandler.unregisterErrorCallback(handleUnhandledError);
    };
  }, []);

  // Criando o router usando createBrowserRouter
  const router = createBrowserRouter([
    // Rotas pblicas
    {
      path: "/",
      element: <Navigate to="/login" replace />
    },
    {
      path: "/login",
      element: <LoginPage />
    },
    {
      path: "/register",
      element: <RegisterPage />
    },
    {
      path: "/forgot-password",
      element: <ForgotPasswordPage />
    },
    
    // Rotas protegidas
    {
      path: "/",
      element: (
        <ProtectedLayout>
          <MainLayout />
        </ProtectedLayout>
      ),
      children: [
        {
          path: "home",
          element: <HomePage />
        },
        {
          path: "onboarding",
          element: <AuthOnboardingPage />
        },
        {
          path: "plans",
          element: <StudyPlansPage />
        },
        {
          path: "study/onboarding",
          element: <StudyOnboardingPage />
        },
        {
          path: "bible",
          element: <BibleExplorerPage />
        },
        {
          path: "bible/:bookId",
          element: <BibleExplorerPage />
        },
        {
          path: "bible/:bookId/:chapterId",
          element: <BibleExplorerPage />
        },
        {
          path: "chat",
          element: <ChatPage />
        },
        {
          path: "chat/:sessionId",
          element: <ChatPage />
        },
        {
          path: "settings",
          element: <SettingsPage />
        },
        {
          path: "gamification",
          element: <GamificationPage />
        },
        {
          path: "monetization",
          element: <MonetizationPage />
        }
      ]
    },
    
    // Rota 404
    {
      path: "*",
      element: <NotFoundPage />
    }
  ]);

  return (
    <ErrorBoundary>
      {/* Componente para exibir notificaes toast */}
      <Toaster
        position="top-right"
        toastOptions={{
          duration: 5000,
          style: {
            borderRadius: '8px',
            background: '#333',
            color: '#fff',
          },
        }}
      />
      
      {/* Rotas da aplicao usando RouterProvider */}
      <RouterProvider router={router} />
    </ErrorBoundary>
  );
};

export default App;
 